<template>
  <div>
    <canvas ref="myCanvas"></canvas>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { CSS2DRenderer, CSS2DObject } from 'three/addons/renderers/CSS2DRenderer.js';
import { GUI } from 'three/addons/libs/lil-gui.module.min.js';
export default {
  setup() {
    const myCanvas = ref(null)
    onMounted(() => {
      var gui;
      const scene = new THREE.Scene()
      const camera = new THREE.PerspectiveCamera(
        75,
        window.innerWidth / window.innerHeight,
        0.1,
        1000
      )
      const renderer = new THREE.WebGLRenderer({
        canvas: myCanvas.value
      })
      renderer.setSize(window.innerWidth, window.innerHeight)
      const material = new THREE.MeshBasicMaterial({ color: 0x808080 });

      // //创建一个圆环的形状
      // var shape = new THREE.Shape();
      // var innerRadius = 4; //内径
      // var outerRadius = 5; //外径
      // var height = 10; //高度

      // shape.absarc(0, 0, outerRadius, 0, Math.PI, true); //外圆
      // shape.absarc(0, 0, innerRadius, 0, Math.PI , true); //内圆
      // shape.lineTo(outerRadius, 0); //连接最后一条线段

      // //创建一个ExtrudeGeometry，并指定形状和设置参数
      // var extrudeSettings = {
      //   steps: 1, //拉伸次数
      //   depth: height, //拉伸深度
      //   bevelEnabled: false, //不开启斜角
      //   material: 0, //使用默认材质
      //   extrudeMaterial: 1 //设置挤出面的材质索引
      // };

      // var geometry = new THREE.ExtrudeGeometry(shape, extrudeSettings);



      var innerRadius = 3; // 内半径
      var outerRadius = 4; // 外半径
      var height = 2; // 高度
      var angleStart = 0; // 起始角度
      var angleLength = Math.PI / 3; // 弧长
      var curve = new THREE.Shape(); // 创建形状对象
      curve.moveTo(innerRadius, 0); // 移动到起点
      curve.absarc(0, 0, outerRadius, angleStart, angleStart + angleLength, false); // 画弧
      curve.lineTo(innerRadius * Math.cos(angleStart + angleLength), innerRadius * Math.sin(angleStart + angleLength)); // 连接起点与终点
      curve.absarc(0, 0, innerRadius, angleStart + angleLength, angleStart, true); // 画内弧
      curve.lineTo(innerRadius, 0); // 连接终点与起点

      var extrudeSettings = { depth: height, bevelEnabled: false }; // 挤压参数
      var geometry = new THREE.ExtrudeGeometry(curve, extrudeSettings); // 创建几何体

      var mesh = new THREE.Mesh(geometry, material);
      mesh.position.set(0, 0, 0);
      var rotationAngle = Math.PI * 1.4;
      mesh.rotation.set(0, 0, rotationAngle);
      scene.add(mesh);
      var testDiv = document.createElement('div');
      testDiv.className = 'label';
      testDiv.textContent = '盾构机管片编号1';
      testDiv.style.marginTop = '-1em';
      testDiv.style.color = 'white';
      var moonLabel = new CSS2DObject(testDiv);
      moonLabel.position.set(2, 0, 0);
      mesh.add(moonLabel);
      var labelRenderer = new CSS2DRenderer();
      labelRenderer.setSize(window.innerWidth, window.innerHeight);
      labelRenderer.domElement.style.position = 'absolute';
      labelRenderer.domElement.style.top = 0;
      document.body.appendChild(labelRenderer.domElement);
      const controls = new OrbitControls(camera, labelRenderer.domElement)
      // camera.position.set(0, 20, 10);

      camera.position.z = 10


      var addScene = function () {

        for (var i = 0; i < 10; i++) {
          var a = new THREE.Mesh(geometry, material);
          scene.add(a);
          a.position.set(0, i * 6, 0);
          var rotationAngle = Math.PI * 1.4;
          a.rotation.set(0, 0, rotationAngle);

          var testDiv2 = document.createElement('div');
          testDiv2.className = 'label';
          testDiv2.textContent = '盾构机管片编号' + i;
          testDiv2.style.marginTop = '-1em';
          testDiv2.style.color = 'white';
          var moonLabel2 = new CSS2DObject(testDiv2);
          moonLabel2.position.set(0, 0, 0);
          a.add(moonLabel2);

          camera.position.set(0, 0, 50);
        }



      }


      function animate() {
        requestAnimationFrame(animate);
        controls.update();
        renderer.render(scene, camera);
        labelRenderer.render(scene, camera);
      }

      animate();

      const layers = {

        '修改管片信息': function () {
          camera.layers.toggle(0);
        },
        '添加十个管片': function () {
          addScene()
        },
       

        'Disable All': function () {

          camera.layers.disableAll();

        }

      };
      function initGui() {

        gui = new GUI();

        gui.add(layers, '修改管片信息');
        gui.add(layers, '添加十个管片');      
        gui.add(layers, 'Disable All');




      }
      initGui()


      // const raycaster = new THREE.Raycaster();
      // const mouse = new THREE.Vector2();
      // function onMouseMove(event) {
      //   // 获取鼠标位置
      //   mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
      //   mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
      // }
      // function onClick(event) {
      //   console.log(event)
      //   // 设置摄像机位置
      //   camera.position.set(0, 20, 25);
      //   controls.update();
      //   // 射线检测
      //   raycaster.setFromCamera(mouse, camera);
      //   const intersects = raycaster.intersectObjects(scene.children);

      //   if (intersects.length > 0) {
      //     // 在此处添加代码，弹出模态框等操作
      //     alert('你点击了图形!');
      //   }
      // }

      // window.addEventListener('mousemove', onMouseMove, false);
      // window.addEventListener('click', onClick, false);

    })

    return {
      myCanvas
    }
  }
}
</script>
